<template>
  <div id="collection-change-order-dialog-component">
    <el-dialog title="Change Order" v-model="visible" width="60%" top="45px">
      <div class="row" v-if="collectionFabrics.length">
        <Draggable
          v-model="collectionFabrics"
          @start="drag = true"
          @end="drag = false"
          item-key="id"
          tag="transition-group"
          >>
          <template #item="{ element }">
            <div class="col">
              <FabricCard :data="element.fabric" card-mode></FabricCard>
            </div>
          </template>
        </Draggable>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="visible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onDoneClick">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import Draggable from "vuedraggable";

import FabricCard from "@/components/FabricCard.vue";

import { CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "ChangeOrderDialog",
  components: {
    Draggable,
    FabricCard
  },
  setup(_, { emit }) {
    const visible = ref(false);
    const collectionFabrics = ref<CollectionFabrics[]>([]);

    const open = async (data: CollectionFabrics[]) => {
      collectionFabrics.value = data;
      visible.value = true;
    };

    const onDoneClick = async () => {
      const orders: number[] = collectionFabrics.value.map(
        (it: CollectionFabrics) => it.fabric.id
      );
      emit("confirm", orders);
      visible.value = false;
    };

    return {
      collectionFabrics,
      visible,

      open,
      onDoneClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#collection-change-order-dialog-component
  .row
    flex(flex-start)
    flex-wrap wrap
    margin 0 -1% 60px
    .col
      width 18%
      margin 0 1% 60px
</style>
